  button.animated-button.thar-one {
    color: #fff;
    background-color: #358CCE;
    margin-top: 20px;
    padding: 10px;
    border-radius: 20%;
    cursor: pointer;
    display: block;
    position: relative;
    border: 2px solid #F7CA18;
    transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
  }
  button.animated-button.thar-one:hover {
    color: #000 !important;
    background-color: transparent;
    text-shadow: none;
  }
  button.animated-button.thar-one:hover:before {
    bottom: 0%;
    top: auto;
    height: 100%;
  }
  button.animated-button.thar-one:before {
    display: block;
    position: absolute;
    left: 0px;
    top: 0px;
    height: 0px;
    width: 100%;
    z-index: -1;
    content: '';
    color: #000 !important;
    background: #F7CA18;
    transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
  }
  